"use client";

import { useEffect } from "react";
import { useRealtimeSync } from "@/contexts/realtime-sync-context";

/**
 * Intercepts clicks on admin's side when they're controlling a user
 * Prevents local execution and only syncs cursor position
 * The actual click happens on the user's side via AdminCursorSimulator
 */
export function AdminClickInterceptor() {
  const { viewingAsUserId } = useRealtimeSync();

  useEffect(() => {
    if (!viewingAsUserId) return; // Only intercept when controlling someone

    const handleClick = (e: MouseEvent) => {
      // Check if this is a real user click (not programmatic)
      if (e.isTrusted) {
        console.log('[AdminInterceptor] Intercepting admin click at', e.clientX, e.clientY);

        // Prevent the click from executing locally
        e.preventDefault();
        e.stopPropagation();
        e.stopImmediatePropagation();

        // The click will be synced via cursor position (isClicking=true)
        // and executed on user's side by AdminCursorSimulator
      }
    };

    // Capture phase to intercept before any handlers
    document.addEventListener('click', handleClick, true);

    console.log('[AdminInterceptor] Active - admin clicks will be synced to user');

    return () => {
      document.removeEventListener('click', handleClick, true);
      console.log('[AdminInterceptor] Deactivated');
    };
  }, [viewingAsUserId]);

  return null; // This component doesn't render anything
}
